    AutoUpload,
    ManualAddFile,
    DefaultFileList,
    SimpleStyle,
    BreakpointResume
  },

  watch: {
    '$i18n.locale': {
      handler () {
        this.$nextTick(() => {
          let preEls = document.querySelectorAll('.demo-description pre');
          for (let i = 0; i < preEls.length; i++) {
            let preEl = preEls[i];
            if (preEl.className.includes('language-')) {
              prism.highlightElement(preEl);
            }
          }
        });
      },
      immediate: true,
    }
  },

  data () {
    return {
      nodejsDemoTab: 'serverjs',
      serverjsCode: '',
      packageJsonCode: '',

      batchFileCount: 3,
      batchChunkCount: 5,
      batchChunkSize: 3,

      testImgBase64: '',
      byteArrMimeType: '',
      customFileAutoUpload: false,
      testByteArr: [],
      defaultFileList: [
        {
          id: 'file1',
          name: 'base64-file.png',
          previewUrl: testImgBase64
        },
        {
          id: 'file2',
          name: 'url-file.png',
          previewUrl: 'https://www.baidu.com/img/PCtm_d9c8750bed0b3c7d089fa7d55720d6cf.png'
        },
        {
          id: 'file3',
          name: 'a-text-file.txt'
        },
        {
          id: 'file4'
        }
      ],

      uploadedChunkIndexes: '0,3,4,7,10,11,12,15'
    };
  },

  computed: {},

  methods: {
    onFileAdded (file) {
      console.log(this.$t('debug.fileAdded'), file);

      /* const reader = new FileReader();
      reader.onload = function(e) {
        const arrayBuffer = e.target.result; // ArrayBuffer
        const byteArray = new Uint8Array(arrayBuffer); // Uint8Array
        const arr = [];
        for (let i = 0; i < byteArray.length; ++i) {
